import React, { useState } from 'react';
import Tic from "./modules/Tic";

const App =(props)=>{
    const [State,Setstate]=useState(false);
    const [stt,setstt]=useState(true);
    const Change = () =>{
        Setstate(true);
        setstt(false);
    }
    return(
        <>
            <div className="button">
            {stt?<button onClick={Change}>Tic Tac Toe</button>:null}
            {stt?<button onClick={Change}>Snake and Ladders</button>:null}
            {stt?<button onClick={Change}>Math quiz</button>:null}
            {stt?<button onClick={Change}>English Quiz</button>:null}
            
            {State?<Tic/>:null}    
            {/* <Tic/> */}
            </div>
        </>
    );
}
export default App;